.buttons {
    background-color: rebeccapurple;
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.button {
    text-align: center;
    width: 100px;
    border: 1px black solid;
    border-radius: 14px;
    padding: 10px;
}

.stop {
    background-color: aqua;
}

.start {
    background-color: bisque;
}

.button:active {
    background-color: red;
}