.timer {
    flex: 1;
    background-color: red;
    display: flex;
    justify-content: center;
}

.digits {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 3rem;
    margin-top: 50px;
}

.mili-sec {
    color: blue;
}

.time {
    align-self: center;
}

.blink {
    animation: blink-animation 1s steps(2, start) 3;
    -webkit-animation: blink-animation 1s steps(2, start) 3;
  }
  @keyframes blink-animation {
    to {
      visibility: hidden;
    }
  }
  @-webkit-keyframes blink-animation {
    to {
      visibility: hidden;
    }
}